@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Serif+Display:ital@0;1&display=swap);
body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app-root .content-root{margin-top:4.8em}.headbar{background-color:#000000 !important;height:4.8em !important;border-bottom:0.1px #d4d4d4 solid}.headbar .toolbar{height:4.8em !important;display:flex;justify-content:space-between}.headbar .toolbar .logo-area{display:flex}.headbar .toolbar .logo-area .logoHeadbar{font-family:'Roboto', sans-serif;font-weight:400;font-size:1.4em;margin-left:1em;margin-right:1em}.headbar .toolbar .button{color:white;border-color:grey}.headbar .toolbar .button .button-text{font-family:'Roboto', sans-serif;font-weight:300;font-size:1.2em;text-transform:lowercase !important}.headbar .toolbar .button .button-text-author{font-family:'DM Serif Display', serif;margin-left:0.3em;font-size:1.2em}

.input-component{padding-top:3em;display:flex;justify-content:center}.input-component .text-field{display:flex;height:3.5em}.input-component .text-field .prefix-area{height:3.5em;display:flex;flex-direction:column;justify-content:center}.input-component .text-field .prefix-area .prefix-text{font-family:'Roboto', sans-serif;font-weight:600;color:darkGrey;font-size:x-large;padding-left:1em;padding-right:1em}.input-component .text-field .input-base{max-width:30em;width:20em}.input-component .text-field .search-button-area{height:3.5em;display:flex;flex-direction:column;justify-content:center}

.palette-block-component{display:flex}.palette-block-component .value-text-container{height:3em;display:flex;flex-direction:column;justify-content:center}.palette-block-component .value-text-container .value-text{width:3em}.palette-block-component .palette-block{height:3em;margin-left:0.5em;margin-right:1em;width:30em}.palette-block-component .hex-text-container{height:3em;display:flex;flex-direction:column;justify-content:center}.palette-block-component .hex-text-container .hex-text-button .hash-text{margin-right:0.2em}.palette-block-component .hex-text-container .hex-text-button .hex-text{font-family:'Roboto', sans-serif;font-weight:300}


.swatch-component{margin-top:3em;display:flex;justify-content:center}

.app-root .content-root{margin-top:4.8em}

